.contactPage .container {
  padding: 50px 20px;
  text-align: center;
}
.contactPage .container .intro {
  margin-bottom: 40px;
  color: #555;
}
.contactPage .container .contact-content {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
  align-items: normal;
  justify-content: center;
}
.contactPage .container .contact-content form {
  flex: 1 1 400px;
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  text-align: right;
}
.contactPage .container .contact-content form .inputContainer {
  margin-bottom: 20px;
}
.contactPage .container .contact-content form .inputContainer label {
  display: block;
  margin-bottom: 5px;
  color: #000;
  font-weight: bold;
}
.contactPage .container .contact-content form .inputContainer input,
.contactPage .container .contact-content form .inputContainer textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid white;
  border-radius: 5px;
}
.contactPage .container .contact-content .contact-info {
  flex: 1 1 400px;
  text-align: right;
}
.contactPage .container .contact-content .contact-info h3 {
  color: #228b22;
  margin-bottom: 20px;
}
.contactPage .container .contact-content .contact-info ul li {
  margin-bottom: 15px;
}
.contactPage .container .contact-content .contact-info ul li i {
  margin-left: 10px;
  color: #228b22;
}
.contactPage .container .contact-content .contact-info .map-container {
  margin-top: 20px;
}
.contactPage .container .contact-content .contact-info .map-container iframe {
  width: 100%;
  height: 300px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}